import React, { useEffect, useState, useRef } from "react";
import {
  ChoiceGroup,
  DefaultButton,
  PrimaryButton,
} from "office-ui-fabric-react/lib/index";
import { MyTeam, readTeamInfoAsGuest } from "../../helpers/OfficeGraph";
import "./style.css";
import { navigate } from "@reach/router";
export default function StartDateTeamSelectedPage(props) {
  const [team, setTeam] = useState(null);
  const [channels, setChannels] = useState([]);
  useEffect(() => {
    async function load() {
      {
        MyTeam(props.teamid).then(async (_team) => {
          setTeam(_team);
          var info = await readTeamInfoAsGuest(_team);
          if (info && info.data && info.data.channels) {
            setChannels(info.data.channels);
          }
        });
      }
    }

    load();
  }, []);

  return (
    <div className="homePage">
      <div>
        <div style={{ minHeight: "50vh" }}>
          {team && (
            <div>
              <h1>{team.displayName}</h1>
            </div>
          )}
          {channels && (
            <div>
              {channels.map((channel) => {
                return (
                  <div>
                    <h3>{channel.displayName}</h3>
                    {channel.description &&
                      channel.description !== channel.displayName && (
                        <div>{channel.description}</div>
                      )}
                  </div>
                );
              })}
            </div>
          )}
        </div>
        <div className="speedDateActions">
          <DefaultButton
            onClick={() => {
              navigate(`/start`);
            }}
          >
            Previous
          </DefaultButton>
          <PrimaryButton
            disabled={team === null}
            onClick={() => {
              navigate(`/start/${team.id}/`);
            }}
          >
            Next
          </PrimaryButton>
        </div>
      </div>
    </div>
  );
}
