function showException(props) {
  ReactDOM.render(
    <Customizer {...FluentCustomizations}>
      <Fabric theme={x}>
        <ErrorBoundary>
          <div
            style={{
              marginTop: "100px",
              maxWidth: "1024px",
              marginLeft: "auto",
              marginRight: "auto",
            }}
          >
            <h1> Sorry, can't get you in</h1>
            <h3>{props && props.errorMessage}</h3>
            <p>
              The following checks might give you and indication of what is
              wrong
            </p>
            <Onboard />
            <p>Suggested actions</p>
            <DefaultButton
              onClick={() => {
                localStorage.clear();
                sessionStorage.clear();
                window.location.href = "/";
              }}
            >
              Clear cache and login
            </DefaultButton>
          </div>
        </ErrorBoundary>
      </Fabric>
    </Customizer>,
    document.getElementById("root")
  );
}

