function InteractionRequired(props) {
  const [showWarning, setShowWarning] = useState(true);
if (props && props.noauthPaths){
  var skipAuth = false
  var currentPath = window.location.pathname
  props.noauthPaths.forEach(noauthPath => {
    
    if (_.isEqual(currentPath,noauthPath)){
      skipAuth = true
    }
  });
  if (skipAuth) return null
}
  if (!props.interactionRequired) return null;

  function backgroundCheck() {
    if (!isAccessTokenExpired) {
      window.location.reload();
      return;
    }
    //if (storageProvider.getItem(""))
    setTimeout(backgroundCheck, 1000);
  }

  //backgroundCheck()
  return (
    <div style={{ position: "fixed", width: "100vw", zIndex: 10 }}>
      <MessageBar
        messageBarType={MessageBarType.error}
        isMultiline={false}
        onDismiss={() => {
          setShowWarning(false);
        }}
        dismissButtonAriaLabel="Close"
        actions={
          <div>
            {!inIframe() && (
              <MessageBarButton
                onClick={() => {
                  selectAccount();
                }}
              >
                {" "}
                Sign in
              </MessageBarButton>
            )}

            {inIframe() && (
              <>
                <a href="#" target="_blank">
                  Click here to open in new tab and sign in
                </a>
                {false && (
                  <MessageBarButton
                    onClick={() => {
                      window.Office.context.ui.displayDialogAsync(
                        "https://localhost:3002"
                      );
                      //window.location.reload();
                    }}
                  >
                    {" "}
                    Reload
                  </MessageBarButton>
                )}
              </>
            )}
          </div>
        }
      >
        We need to sign you in
      </MessageBar>
    </div>
  );
}
function getUserAndLicense() {
  return new Promise(async (resolve, reject) => {
    var auth = await authenticate(
      config,
      (search, search2, interactionRequired) => {
        return resolve({ interactionRequired });
        var errorMessage =
          search && search.error_description
            ? search.error_description
            : search2 && search2.error_description
            ? search2 && search2.error_description
            : "Unknown error";
        showException({ errorMessage });
        // this is where you can send a signal to a host that intercati ons is required
      }
    ).catch((error) => {
      reject(error);
    });
    if (!auth) return;

    var license = await getLicenseForUPN(
      auth && auth.user ? auth.user.userPrincipalName : null
    ).catch((e) => {
      console.log("getLicenseForUPN", e);
    });

    who(auth.user.userPrincipalName);
    const key = "HASLANDED";

    var landed = storageProvider.getItem(key);
    if (!landed) {
      trace(EVENT.LANDING);
      storageProvider.setItem(key, "1");
    }
    resolve({ auth, license });
  });
}

function SpeedDateApp() {
  const [auth, setAuth] = useState(null);

  const [license, setLicense] = useState(null);
  const [interactionRequired, setInteractionRequired] = useState(false);
  // let { userContext, dispatch } = React.useContext(UserContext);
  useEffect(() => {
    getUserAndLicense()
      .then((result) => {
        if (result.interactionRequired) {
          setInteractionRequired(true);
        } else {
          setAuth(result.auth);
          setLicense(result.license);
        }
      })
      .catch((error) => {
        console.log("getUserAndLicense", error);
      });
  }, []);
  return (
    <div>
      <Customizer {...FluentCustomizations}>
        <Fabric theme={x}>
          <ErrorBoundary>
            <InteractionRequired interactionRequired={interactionRequired} noauthPaths={["/"]} />
            <Router>
              <Onboard path="/check" />
              <GotoDefault auth={auth} path="/default" />
              <HomePage path="/" />

              <StartDatePage auth={auth} path="/start" />
              
              
              <StartDateTeamSelectedPage path="/start/:teamid"/>
              <Home auth={auth} path="/home" />
              <App auth={auth} path="/board/:board" signOut={signOut} />
              <GetAdminConsented path="/getadminconsent" />
              <AdminConsented path="/adminconsented" />
              <SignedOut path="/signedout" />
              <SignOut path="/signout" />
              <Debug path="/debug" />
              <NoPage default />
            </Router>
          </ErrorBoundary>
        </Fabric>
      </Customizer>
      <div style={{position:"fixed",bottom:"0"}}>
      {pack.version}
      </div>
    </div>
  );
  
}

async function start() {
  if (window.location.hash.indexOf("error=") > -1) {
    authenticate(config);
  } else {
    ReactDOM.render(<SpeedDateApp />, document.getElementById("root"));
  }
}
